"use client";

import { t } from "../../../utils/translate";
import BreadCrumps from "../_components/breadcrumps";

const links = [
  {
    name: t("plantation.p1"),
    href: "/",
  },
  {
    name: t("plantation.p2"),
    href: "/plantation",
  },
];

const Plantation = () => {
  return (
    <div className="main-container">
      <BreadCrumps links={links} />

      <div className="w-full flex justify-between items-center mt-[50px]">
        <div className="w-[48%]">
          <p className="bold-description">
            Наша плантация находится в 100 км на юг от столицы Эквадора Кито,
            недалеко от городка Латакунга. На благодатных склонах вулкана
            Котопакси, до которого всего 30 км напрямую, сама природа
            позаботиласть об идеальных условиях для выращивания роз.
          </p>
          <p className="light-description mt-[30px]">
            Кол-во сортов:
            ..............................................................................................
            14 Площадь плантации:
            ...........................................................................
            5 га Высота над уровнем
            моря:........................................................ 2800 м
            Кол-во сотрудников:......................... 25 рабочих + 2
            менеджера
          </p>
          <div>
            <button className="button">Войти в кабинет партнера</button>
          </div>
        </div>
        <div className="w-[48%]"></div>
      </div>
    </div>
  );
};

export default Plantation;
